<template>
  <div>
    <m-form
        title=" "
        :showList="false"
        :listRoute="{name:'PersonnelList'}"
        :exportBaseUrl="printUrl"
        :exportParams="{id: item.id}"
        :canDelete="false"
        :canSubmit="canSubmit"
        :isEditing.sync="isEditing"
        @submit="submit"
        @delete="deleteItem"
        @clearForm="clearForm()"
        ref="workshopForm"

    >

      <template>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field label="* " v-model="item.name" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-model="item.last_name" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-model="item.father_name" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label=" " v-model="item.personnel_code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label="* "
                :items="NATIONALITY_TYPE"
                v-model="item.nationality"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="* " v-model="item.country" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label="* "
                :items="GENDER_TYPE"
                v-model="item.gender"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label="*  "
                :items="MILITARY_SERVICE_STATUS"
                v-model="item.military_service"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-model="item.identity_code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-model="item.national_code" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label="*  "
                :items="MARITAL_STATUS_TYPES"
                v-model="item.marital_status"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="  " v-model="item.number_of_childes" readonly  background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <date v-model="item.date_of_birth" label="*  " :default="true" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <date v-model="item.date_of_exportation" label="*   " :default="true" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="   " v-model="item.location_of_birth"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="   " v-model="item.location_of_exportation"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="  " v-model="item.sector_of_exportation"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="1">
            <v-text-field label="*  " v-model="item.city_phone_code"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="*   " v-model="item.phone_number"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="7">
            <v-text-field label="*  " v-model="item.address"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-text-field label="*   " v-model="item.postal_code"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*   1" v-model="item.mobile_number_1" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="   2 " v-model="item.mobile_number_2" background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="2">
            <v-switch
                v-model="item.insurance"
                label= '   '
                :disabled="!isEditing"
            ></v-switch>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field label="  " v-model="item.insurance_code"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label="*  "
                :items="DEGREE_TYPE"
                v-model="item.degree_education"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-model="item.field_of_study"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label=" "
                :items="UNIVERSITY_TYPES"
                v-model="item.university_type"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label=" " v-model="item.university_name"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*   " v-model="item.account_bank_name"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*   " v-model="item.account_bank_number"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*   " v-model="item.bank_cart_number"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field label="*  " v-model="item.sheba_number"   background-color="white" :disabled="!isEditing"/>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
                label="*   "
                :items="PERSONNEL_STATUS"
                v-model="item.is_personnel_active"
                item-text="name"
                item-value="value"
                :disabled="!isEditing"
            />
          </v-col>

        </v-row>
      </template>
      <v-btn
          class="light-blue white--text mt-6  mr-2 float-left"
          @click="verifyPersonnel(item.id)"
          v-if="item.id && !item.verified" > </v-btn>
      <v-btn
          :disabled="true"
          class="green white--text mt-6  mr-2 float-left"
          v-if="item.id && item.verified" >  </v-btn>

    </m-form>

  </div>

</template>

<script>

import TenderList from "@/modules/contracting/tender/TenderList";
import {MFormMixin} from "@/components/m-form";
import mtime from "@/components/mcomponents/cleave/Time";
import FormsMixin from "@/mixin/forms";
import FactorMixin from "@/views/panel/factor/mixin";
import TreeSelect from "@/components/selects/TreeSelect";
import {PathLevels, VisitorLevels} from "@/variables";
import citySelect from "@/components/selects/CitySelect";
import MDatatable from "@/components/m-datatable";
import formsMixin from "@/mixin/forms";
import money from "@/components/mcomponents/cleave/Money";
import date from "@/components/mcomponents/cleave/Date";


import TransactionForm from "@/views/panel/transaction/Form";
import LadingMixin from "@/modules/dashtbashi/LadingMixin";


export default {
  name: "PersonnelForm",
  mixins: [MFormMixin, LadingMixin, formsMixin, FormsMixin, FactorMixin],
  components: {mtime, TreeSelect, citySelect, TenderList, MDatatable, TransactionForm, money},
  props: {
    id: {},
  },
  data() {
    return {
      NATIONALITY_TYPE: [
        {name: ' ', value: 1},
        {name: ' ', value: 2},
      ],
      PERSONNEL_STATUS: [
        {name: ' ', value: true},
        {name: ' ', value: false},
      ],
      GENDER_TYPE: [
        {name: ' ', value: 'm'},
        {name: '', value: 'f'},
      ],
      MILITARY_SERVICE_STATUS: [
        {name: '  ', value: 'd'},
        {name: ' ', value: 'n'},
        {name: '', value: 'e'},
        {name: ' ', value: 'x'},
      ],
      MARITAL_STATUS_TYPES: [
        {name: ' ', value: 's'},
        {name: '', value: 'm'},
        {name: ' ', value: 'c'},
      ],
      DEGREE_TYPE: [
        {name: '  ', value: 1},
        {name: '  ', value: 2},
        {name: ' ', value: 3},
        {name: '', value: 4},
        {name: '', value: 5},
        {name: ' ', value: 6},
        {name: '', value: 7},
        {name: ' ', value: 8},
      ],
      UNIVERSITY_TYPES: [
        {name: ' ', value: 'st'},
        {name: '', value: 'op'},
        {name: ' ', value: 'np'},
      ],


      printUrl: 'payroll/personnel/all',
      isWorkshopConfirmed: false,
      worshopId: 0,
      baseUrl: "payroll/personnel",
      permissionBasename: "personnel",
      appendSlash: true,
      hasList: false,
      hasIdProp: true,
      hasLock: true,
      isDefinable: true,
      myClass: '',
      factors: [],
      PathLevels,
      VisitorLevels,
      paymentDialog: false,
      payment: '',
      performClearForm: true,
    };
  },
  computed: {
    headers() {
      return [
        {
          text: "",
          value: "name",
        },
        {
          text: " ",
          value: "last_name",
        },
        {
          text: " ",
          value: "father_name",
        },
        {
          text: " ",
          value: "personnel_code",
        },
        {
          text: "",
          value: "nationality",
        },
        {
          text: "",
          value: "gender",
        },
        {
          text: " ",
          value: "military_service",
        },
        {
          text: " ",
          value: "identity_code",
        },

        {
          text: " ",
          value: "national_code",
        },
        {
          text: " ",
          value: "marital_status",
        },
        {
          text: " ",
          value: "number_of_childes",
        },
        {
          text: " ",
          value: "date_of_birth",
        },
        {
          text: "  ",
          value: "date_of_exportation",
        },
        {
          text: " ",
          value: "location_of_birth",
        },
        {
          text: "  ",
          value: "location_of_exportation",
        },
        {
          text: "  ",
          value: "sector_of_exportation",
        },
        {
          text: " ",
          value: "city_phone_code",
        },
        {
          text: " ",
          value: "phone_number",
        },
        {
          text: "",
          value: "address",
        },
        {
          text: " ",
          value: "postal_code",
        },
        {
          text: "  1",
          value: "mobile_number_1",
        },
        {
          text: "  2",
          value: "mobile_number_2",
        },
        {
          text: "  ",
          value: "insurance",
        },
        {
          text: " ",
          value: "insurance_code",
        },
        {
          text: " ",
          value: "degree_education",
        },
        {
          text: " ",
          value: "field_of_study",
        },
        {
          text: " ",
          value: "university_type",
        },
        {
          text: " ",
          value: "university_name",
        },
        {
          text: " ",
          value: "account_bank_name",
        },
        {
          text: "  ",
          value: "account_bank_number",
        },
        {
          text: "  ",
          value: "bank_cart_number",
        },
        {
          text: " ",
          value: "sheba_number",
        },
      ];

    },
  },

  methods: {
    verifyPersonnel(id){
      this.request({
        url: this.endpoint(`payroll/personnel/verify/` + id + '/'),
        method: "get",
        success: data => {
          console.log(data);
          this.notify('     ', 'success')
          window.location.reload();
        },
        error: data => {
          this.notify(data.response.data[0].messages[0], 'warning')

        }
      })

    },
    to(item) {
      return {
        name: 'WorkshopDetail',
        params: {
          id: item.id,
        },
      };
    },

    unConfirm() {
      this.$router.go()
      this.notify('    ', 'warning')
    },
  },
}
</script>

<style scoped lang="scss"></style>

